export default function renderPartners(arr){
  const wrapper = document.querySelector(".partners-support");
 
  if(wrapper){
     const parent = wrapper.querySelector(".swiper-wrapper");
     parent.appendChild(render(arr));
  }
 
  // render(arr);
}
function render(arr){
  const fragment = document.createDocumentFragment();
  arr.forEach((item) => {
    try {
      let { link, photo,  svg } = item;

      let photoLink = item['photo-link'];
      renderImg (photo, svg, photoLink)
      const div = document.createElement("div");
      div.classList = "swiper-slide";
      div.innerHTML = `
      <a href="${link}" class="partners-support__item ${link.includes('facebook') ? 'partners-support__item-fb' : ''}">
      ${renderImg (photo, svg, photoLink)}
      </a>`;
      fragment.appendChild(div);
      return fragment;
    } catch (e) {
      console.log(e);
    }
  });
  return fragment;
}

function renderImg (photo, svg, photoLink){
  if( photo || photoLink) {
    let url = photo ? photo : photoLink;
    return `<img src="${url}"  alt="Partner">`
  }
  if( svg) {
    return `<svg class="icon">
    <use xlink:href="#${svg}"></use>
    </svg>`
  }
  // if( photoLink) {
  //   console.log(photoLink);
  // }
}

