export default function renderTeam(arr) {
  const parent = document.querySelector(".crew");
  if(parent){
      parent.appendChild(render(arr));
  }

}

function render(arr) {
  const fragment = document.createDocumentFragment();
  arr.forEach((item) => {
    try {
      let { name, nameEn, role, roleEn, instagram, photo } = item;

      const div = document.createElement("div");
      div.classList = "crew__item card bdr-12";
      div.innerHTML = `
      ${renderImg(photo, name, nameEn)}
    
                  
                          <div class="name fz-18 f-800">
                          ${!isEn ? name : nameEn}
                           </div>
                           <div class="role t-secondary">      
                           ${!isEn ? role : roleEn}
                            </div>
                         <a href="${instagram}" class="t-upper ">${
        !isEn ? "<span>Зв’язатися</span>" : "<span>Contact</span>"
      }</a>
                         </div>`;
      fragment.appendChild(div);
      return fragment;
    } catch (e) {
      console.log(e);
    }
  });
  return fragment;
}

function renderImg(photo, name, nameEn) {
  if (photo) {
    return `
    <div class="image pos-r"><img src="${photo}" loading="lazy" alt="${
      !isEn ? name : nameEn
    }"></div>`;
  } else {
    return "";
  }
}
