import { header } from "./elements";
const headerNav = header.querySelector("nav");

const adaptiveElements = () => {
  if (matchMedia("(max-width: 992px)").matches) {

    const burger = document.createElement("button");
    burger.innerHTML = `
    <span></span>
    `;
    burger.classList.add("burger");
    header.querySelector(".header__btns").append(burger);
    burger.addEventListener("click", function () {
      this.classList.toggle("active");
      headerNav.classList.toggle("active");
      document.body.classList.toggle("active");
    });
    const subMenu = header.querySelector('.menu-submenu');
    const menu = header.querySelector('.menu');
    if(subMenu){
         menu.querySelector('.menu__item-sub').insertAdjacentHTML('afterend', subMenu.innerHTML);
         menu.querySelectorAll('.menu__item').forEach(item => {
           if (!item.classList.contains('f-600')) {
item.classList.add('f-600')
           }
         })
    }
 
    // subMenu.remove();
   
    menu.querySelectorAll('.menu__item').forEach(item => {
     
      if(item.innerText === 'Публікації'){
        
        item.firstChild.innerText = "Новини"
      }
    })
    if( menu.querySelector('.menu__item-sub')){
       menu.querySelector('.menu__item-sub').remove();
    }
    if(document.querySelector('.objects')){
   
      document.querySelectorAll('.objects-item.done').forEach(item => {
        item.querySelector('.objects-item__done').insertAdjacentElement('beforebegin', item.querySelector('.budget'))
        // console.log(item)
      })
    }


  }
  if (matchMedia("(max-width: 660px)").matches) {


     headerNav.appendChild(header.querySelector('[data-help]'));
     headerNav.appendChild(header.querySelector('[data-telegram]'));

  }
  if (matchMedia("(max-width: 600px)").matches) {
    const logo = header.querySelector('.logo');
   logo.innerHTML = `
   <svg class="icon">
   <use xlink:href="#logo-mobile"></use>
 </svg>`


  }


 


  if (header.querySelector("[data-help]") && matchMedia("(max-width: 700px)").matches) {
    headerNav.append( header.querySelector("[data-help]"));
  }


  // data-help
};
export default adaptiveElements;
