export default function renderCelebrity(arr) {
  const wrapper = document.querySelector(".celebrity");

  if (wrapper) {
     const parent = wrapper.querySelector(".swiper-wrapper");
    parent.appendChild(render(arr));
  }
}

function render(arr) {
  const fragment = document.createDocumentFragment();
  arr.forEach((item) => {
    try {
      let { name, nameEn, instagram, photo } = item;
      const div = document.createElement("div");
      div.classList = "swiper-slide";
      div.innerHTML = `
                        <div class="celebrity__item bdr-12 card">
                          <div class="image">
                            <img src="${photo}" loading="lazy" alt="${!isEn ? name : nameEn}">
                          </div>
                          <div class="name fz-18 f-800">
                          ${!isEn ? name : nameEn}
                          </div>
                          <a href="${instagram}" class="t-upper ">instagram</a>
                        </div>`;
      fragment.appendChild(div);
      return fragment;
    } catch (e) {
      console.log(e);
    }
  });
  return fragment;
}
